<template>    <div class="user-detail-edit">        <h2>编辑个人信息</h2>        <n-space>            <n-space vertical>                <n-space align="center">                    昵称：                    <n-input v-model:value="userDetail.username" type="text" placeholder="用户名"/>                </n-space>                <n-space align="center">                    密码：                    <n-input v-model:value="userDetail.password" type="password" placeholder="不输入为不修改"/>                </n-space>                <n-space size="large" item-style="padding-top: 20px; padding-left: 30px">                    <n-button @click="updateAdminDetail" type="success">保存</n-button>                    <n-button>取消</n-button>                </n-space>            </n-space>        </n-space>    </div></template><script>import notImage from '../assets/notImage.jpg'import {reactive} from 'vue'import {NSpace, NInput, NButton, NAvatar, useMessage} from 'naive-ui'import request from '../utils/request'export default {    name: 'AdminDetail',    setup() {        let user = JSON.parse(localStorage.getItem('user'))        let userDetail = reactive({            username: user.username,            password: '',            id: user.id        })        let message = useMessage()        function updateAdminDetail() {            request.post('/admin/update', userDetail).then(res => {                if (res.data.code === 0) {                    message.success('修改成功')                    localStorage.setItem('user', JSON.stringify(res.data.data))                    location.reload()                } else {                    message.error('修改失败')                }            })        }        return {            userDetail,            notImage,            updateAdminDetail,        }    },    components: {        NSpace,        NInput,        NButton,        NAvatar,    }}</script><style scoped>.user-detail-edit {    padding: 24px;}</style>